import { Carousel } from 'antd'
import { React, useEffect, useState } from 'react'
import { Space, Spin } from 'antd';
// import PubSub from 'pubsub-js';
import axios from 'axios';
import Login from '../Login';
import User from '../User';
import './index.css'


export default function Home() {
  const [contentINFO, setContentInfo] = useState({ content: "", name: "", Loading: false });
  useEffect(() => {
    setTimeout(() => { document.querySelector('h1').style.fontSize = "70px"; document.querySelector('h1').style.marginBottom = "5vh" })
    setTimeout(() => {
      axios('https://47.123.7.73:8000/getsentence').then(
        response => { setContentInfo({ content: response.data.content, name: response.data.name, Loading: true }); document.querySelector(".word").style.animation = "textAppear 2s"; document.querySelector(".word").style.fontSize = Math.floor((Math.random() + 1) * 18 + 5) + "px"; setTimeout(() => { document.querySelector(".word").style.animation = ""; }, 500) },
        error => { console.log(error); }
      )
    }, 1000)
  }, [])

  return (
    <div style={{ textAlign: "center", position: "relative" }}>
      <h1 className="welcom_home" style={{ fontSize: "5px", transition: "1s all" }}>
        <span> </span><span> </span><span> </span><span> </span><span>C</span><span>o</span><span>d</span><span>e</span><span> </span><span>W</span><span>o</span><span>rld</span>
      </h1>
      <Carousel effect="fade" autoplay style={{ position: "absolute", width: "40%", marginTop: "5vh", left: "1vw" }}>
        <div>
          <img src="https://www.dmoe.cc/random.php" alt="" style={{ height: "37.8vh", width: "100%" }} />
        </div>
        <div>
          <img src="https://api.r10086.com/%E5%9B%BE%E5%8C%85webp/%E5%8A%A8%E6%BC%AB%E7%BB%BC%E5%90%882/ChMkJllkKMeIJ6CSAAdGdB9XHNIAAeWIAJf87AAB0aM436.webp" alt="" style={{ height: "37.8vh", width: "100%" }} />
        </div>
        <div>
          <img src="https://api.r10086.com/%E5%9B%BE%E5%8C%85webp/%E5%8A%A8%E6%BC%AB%E7%BB%BC%E5%90%884/0072Vf1pgy1foxkd1pxk2j31hc0u0tqe.webp" alt="" style={{ height: "37.8vh", width: "100%" }} />
        </div>
        <div>
          <img src="https://api.r10086.com/%E5%9B%BE%E5%8C%85webp/%E5%8A%A8%E6%BC%AB%E7%BB%BC%E5%90%8810/9bd9b167gy1fwsfxgg0hkj21hc0u04qp.webp" alt="" style={{ height: "37.8vh", width: "100%" }} />
        </div>
      </Carousel>

      <div style={{ position: "absolute", right: "1vw", height: "38vh", width: "55%", marginTop: "5.5vh", borderRadius: "10px", overflow: "auto" }}>
        <div>
          <Space direction="vertical" style={{ width: '100%', marginTop: "15vh" }} >
            <Spin tip="   . . ." size="large" spinning={!contentINFO.Loading} >
              <div className="content" />
            </Spin>
          </Space>
          <div className='word'>
            <span>{contentINFO.content}</span>
            <br />
            <span style={{ marginLeft: "20vw" }}>{"--" + contentINFO.name}</span>
          </div>
        </div>
      </div>

      {/* ---------------------------------------- */}
      {
        !window.localStorage.getItem("email") ?
          <div className="outLogin" style={{ position: "absolute", height: "580px", width: "100%", top: "60vh", marginTop: "2rem", transition: "2s all" }}>
            <h1 style={{ position: "absolute", top: "18vh", left: "14vw", fontSize: "80px" }}>
              <span> </span><span></span>
            </h1>
            <h1 style={{ position: "absolute", color: "black", top: "35vh", left: "7vw", fontSize: "80px" }}>
              <span> </span><span> </span><span> </span><span> </span><span></span>
            </h1>
            <div className='login' style={{ marginTop: "13vh", textAlign: "center", transition: "1s all", boxShadow: '0px 15px 25px rgba(0,0,0,.5)', backgroundColor: "black", borderRadius: "20px", width: "40%", height: "50vh", marginLeft: "45vw", padding: "3vh 5vw", background: 'rgba(0,0,0,.8)' }}>
              <h2 style={{ color: "skyblue", marginBottom: "5vh" }}> </h2>
              <Login />
            </div>
          </div>
          :
          // 
          <User />
      }
      {/* ------------------------------------------------------------------------------ */}
    </div>
  )
}
