import { React } from 'react';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { Button, Checkbox, Form, Input, notification } from 'antd';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './index.css'



const Login = () => {
    let username;
    let loginInfo;
    let userLoginState;
    let email;

    const [api, contextHolder] = notification.useNotification();
    const myNav = useNavigate()

    const onFinish = async (values) => {
        await axios({
            url: "https://47.123.7.73:8000/login",
            data: [
                values.username,
                values.password
            ],
            method: "POST"
        }).then(
            response => {
                username = response.data.username;
                loginInfo = response.data.message;
                userLoginState = response.data.status;
                if(!userLoginState){
                    return
                }
                email = response.data.email;
                window.localStorage.setItem("email", email.split("@")[0])
                setTimeout(()=>{
                    window.location.reload()
                },2000)
            },
            error => { console.log(error); }
        )
        setTimeout((
            api.open({
                key: "login",
                message: username,
                description: loginInfo,
            })
        ),3000)
        
    };


    return (
        <>
            {contextHolder}
            <Form
                name="normal_login"
                className="login-form"
                initialValues={{
                    remember: true,
                }}
                onFinish={onFinish}
            >
                <Form.Item
                    name="username"
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                    ]}
                >
                    <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="" />
                </Form.Item>
                <Form.Item
                    name="password"
                    rules={[
                        {
                            required: true,
                            message: '',
                        },
                    ]}
                >
                    <Input
                        prefix={<LockOutlined className="site-form-item-icon" />}
                        type="password"
                        placeholder=""
                    />
                </Form.Item>
                <Form.Item>
                    <Form.Item name="remember" valuePropName="checked" noStyle>
                        <Checkbox style={{ color: "lightblue" }}></Checkbox>
                    </Form.Item>

                    {/* <a className="login-form-forgot" href="www.baidu.com">
                        
                    </a> */}
                </Form.Item>

                <Form.Item style={{ color: "white" }}>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        
                    </Button>
                    <span> </span>
                      <span className='addours' onClick={(value) => { myNav(value.target.className) }}></span>
                </Form.Item>
            </Form>
        </>
    );
};
export default Login;
